import Loader from "../Loader/Loader";

import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";

import { setObjectData, setObjectDetails } from "src/store/droppedObjectSlice";
import { setIsDropped, setLoading } from "src/store/droppedObjectSlice";
import {
  initializeDroppableArea,
  getDroppedObjectDetails,
} from "src/services/api/droppableService";

import useToast from "src/hooks/useToast";
import {
  MSG_FETCH_OBJECT_DETAILS_FAILED,
  MSG_INVALID_OBJECT_TYPE,
  MSG_UNEXPECTED_ERROR,
} from "src/utils/toastMessages";

import { useQuery } from "@tanstack/react-query";
import { toast } from "react-toastify";
import { DragAndDropComponent } from "./DragAndDrop";

export const WithDroppableLogic = ({ children }) => {
  const { showErrorToast } = useToast();
  const dispatch = useDispatch();

  const { isDropped, loading } = useSelector((state) => state.droppedObject);

  const fetchObjectDetails = useCallback(
    async (dataItems) => {
      try {
        const objectDetailsResult = await getDroppedObjectDetails({
          dataItems,
        });

        if (objectDetailsResult?.success) {
          dispatch(setObjectDetails(objectDetailsResult?.data?.cardData));
          dispatch(setIsDropped(true));
          console.info("[fetchObjectDetails] ‚úÖ Redux store updated.");
        } else {
          showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
        }
      } catch (error) {
        console.error("[FetchObjectDetails] Error fetching details:", error);
        showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
      } finally {
        console.info("[fetchObjectDetails] üèÅ Ensuring loading is reset.");
        dispatch(setLoading(false));
      }
    },
    [dispatch, showErrorToast]
  );

  const handleDrop = useCallback(
    async (dataItems) => {
      console.info("[handleDrop] handleDrop called with dataItems:", dataItems);

      dispatch(setLoading(true));

      try {
        if (!!!dataItems?.length) {
          return toast.error("No data items to process.");
        }

        const objectType = dataItems?.[0]?.objectType;
        const validTypes = ["VPMReference", "Document", "Raw_Material"];

        if (!validTypes.includes(objectType)) {
          return showErrorToast(MSG_INVALID_OBJECT_TYPE);
        }

        // dispatch(setIsDropped(true));
        dispatch(setObjectData(dataItems));

        await fetchObjectDetails(dataItems);
      } catch (error) {
        console.error("[Drop] Error in handleDrop:", error);

        dispatch(setLoading(false)); // ‚úÖ Reset loading in Redux
        showErrorToast(MSG_UNEXPECTED_ERROR);
      }
    },
    [showErrorToast]
  );

  const { data: droppableContainer, isLoading } = useQuery({
    queryKey: ["droppableContainer"],
    queryFn: () =>
      new Promise((resolve) => {
        const interval = setInterval(() => {
          const container = document.querySelector(".droppable-container");

      toast.loading("rrrrrrrrrrrrrrrrr............................")


          if (container) {
            clearInterval(interval);
            resolve(container);
          } else {
            resolve(false);
          }
        }, 100); // check every 100ms
      }),
  });

  const { isFetching } = useQuery({
    queryKey: ["initdrop"],
    queryFn: async () => {
      if (!isDropped) {
        console.info(
          "[initializeDroppableArea] üöÄ Resetting isDropped to false..."
        );
        dispatch(setIsDropped(false));
      } else {
        console.info(
          "[initializeDroppableArea] ‚è≥ isDropped is already true. Skipping reset."
        );
      }

      toast.loading("loading............................")

      await initializeDroppableArea(
        droppableContainer,
        handleDrop,
        dispatch,
        showErrorToast
      );
    },

    enabled: !!droppableContainer,
  });

  if (loading) return <Loader />;
  if (!isDropped) return <DragAndDropComponent handleDrop={handleDrop} />;

  return children;
};
